ải và bảo rằng nếu còn đánh nhau nữa thì sẽ phạt cả ba, bèn bỏ đi ai về nhà nấy."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Ông Lý thấy ba người đi rồi bèn đi tìm bà Lý bảo:"),_c('br'),_c('br'),_vm._v(" – Vừa rồi có ba người làng đến bảo rằng họ trông thấy trăng đã tròn rồi, vậy bà hãy sửa soạn chúng ta phải lo lễ vật tế đình."),_c('br'),_c('br'),_vm._v(" Bà Lý đáp:"),_c('br'),_c('br'),_vm._v(" – Vâng, cám ơn ông. Nếu ông cho tôi một bộ quần áo mới để mặc Tết, thì tôi muốn tự đi chợ chọn hàng và màu tôi thích. Tôi không ngờ ông cũng nhớ rằng áo tôi rách hết cả rồi."),_c('br'),_c('br'),_vm._v(" Bà Lý sung sướng được may áo mới, vội vàng về nhà gọi cô con gái lớn bảo:"),_c('br'),_c('br'),_vm._v(" – Cha con cho phép mẹ may một chiếc áo mới. Mẹ đã xin phép cha con lên chợ Huyện tự chọn hàng mẹ thích. Con có muốn đi chọn hàng với mẹ thì sửa soạn đi."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Cô con gái mừng quá trả lời:"),_c('br'),_c('br'),_vm._v(" – Thật không mẹ? Mẹ cũng biết đấy, con có còn là bé con nữa đâu! Con lớn đã lâu rồi mà. Nếu me bằng lòng cho đi lấy chồng thì sung sướng quá! Con thương mẹ quá!"),_c('br'),_c('br'),_vm._v(" Cô bé hí hửng sắp được lấy chồng, chạy ra vườn trông thấy vú già bèn ôm chầm lấy vú:"),_c('br'),_c('br'),_vm._v(" – Mẹ bằng lòng gã chồng cho tôi rồi vú ơi. Lễ cưới sẽ cử hành ngay lập tức."),_c('br'),_c('br'),_vm._v(" Vú em cảm động khóc òa lên:"),_c('br'),_c('br'),_vm._v(" – Hu, hu, tôi ở với ông bà đã mấy chục năm nay rồi. Nhờ ơn Trời Phật xui khiến, ông bà cho tôi về quê thăm nhà, tôi cám ơn ông bà hết sức."),_c('br'),_c('br'),_vm._v(" Câu chuyện tạm ngừng tại đây, nhưng sự thực chuyện của bọn họ không thể nào kể hết đư